@import url("https://fonts.googleapis.com/css?family=Nanum+Gothic");
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box; }

html, body {
  height: 100%; }

ul, li {
  list-style: none; }

a {
  text-decoration: none; }

.project-intro p, .my-role p, .used-technique p, .screen-shot-explanation p, .eight-record-explanation p {
  word-break: break-all;
  font-size: 1.1rem; }

.title, .sub-title, .my-intro h2, .my-intro h3 {
  font-weight: lighter; }

#root, .wrap, .container, #intro, #intro > div, .my-pic #my-pic-img, .screen-shot-item > img, .eight-record-item > img {
  width: 100%;
  height: 100%;
  overflow-x: hidden; }

.nav-bar-wrap {
  width: 100%;
  height: 50px;
  background-color: #ffffff;
  box-shadow: 2px 2px #e0e0e0;
  opacity: .8;
  position: fixed;
  z-index: 1; }

.nav-bar {
  width: 60%;
  height: 100%;
  margin: 0 auto;
  text-align: center; }
  .nav-bar li {
    width: 25%;
    height: 100%;
    float: left;
    text-align: center;
    line-height: 47px; }
  .nav-bar a {
    color: #333333; }

.title, .sub-title {
  text-align: center;
  font-size: 2.5rem; }

.sub-title {
  font-size: 2rem; }

.third-title {
  width: 30%;
  float: right;
  font-size: 1.5rem; }

#intro {
  background: url("../images/bg_intro.png");
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  display: table;
  color: #ffffff; }
  #intro > div {
    display: table-cell;
    vertical-align: middle; }

#about {
  width: 100%;
  margin-top: 2.3%; }
  #about .about-box {
    margin: 0 auto;
    width: 60%;
    height: auto;
    display: flex; }

.my-pic {
  width: 40%;
  height: 40%; }
  .my-pic #my-pic-img:hover {
    transform: rotate(360deg);
    transition: all .8s linear; }

.my-intro {
  width: 60%;
  height: auto;
  padding-top: 10%;
  margin-left: 2%; }
  .my-intro h2 {
    font-size: 1.8rem; }
  .my-intro h3 {
    font-size: 1.5rem; }
  .my-intro p {
    font-size: 1.2rem; }
    .my-intro p span {
      font-size: 1.2em; }

#projects {
  width: 100%;
  margin-top: 5%; }
  #projects .project-box {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    width: 60%;
    height: auto;
    margin: 0 auto;
    margin-top: 2%; }
  #projects .project-item {
    width: 480px;
    height: 280px;
    margin: 1%;
    border-radius: 15px;
    background-size: cover;
    background-position: center;
    box-shadow: 5px 5px 5px #C4C4C4; }
  #projects .project-item:hover {
    cursor: pointer;
    transform: scale(1.1);
    transition: all .5s ease; }

#contact {
  width: 100%;
  margin: 5% 0 5% 0;
  position: relative; }
  #contact .contact-box {
    width: 60%;
    margin: 0 auto;
    margin-top: 2%;
    display: flex;
    flex-direction: column;
    text-align: center; }
  #contact .contact-btn {
    border: 1px solid #B4B3B3;
    font-size: 1.5rem;
    padding: 15px;
    margin: 1%;
    border-radius: 15px;
    background-color: transparent; }
  #contact .contact-btn:hover {
    cursor: pointer;
    border: 1.3px solid #55A3A1 !important; }

.project-header {
  width: 100%;
  height: 700px;
  background-position: center;
  background-size: cover; }

.project-intro, .my-role, .used-technique, .screen-shot {
  display: flex;
  flex-direction: column;
  margin: 5% 0 0 15%; }

.my-role h1 {
  font-size: 1.2rem; }

.used-technique h1 {
  font-size: 1.2rem; }

.screen-shot-box {
  width: 70%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin: 0 auto; }

.screen-shot-item {
  width: 900px;
  height: 460px;
  margin: 1%;
  border: #c4c3c3; }

.screen-shot-explanation {
  width: 20%;
  height: auto;
  margin: 1%; }

.project-detail-name {
  margin-top: 3%; }

#html-helper {
  width: 100%;
  height: 1000px; }

#hacker-mon {
  width: 100%;
  height: 800px; }

#eight-record {
  width: 100%;
  height: 800px; }
  #eight-record .eight-record-box-wrap {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    width: 80%;
    margin: 0 auto; }

.eight-record-box {
  display: flex;
  flex-direction: column;
  margin: .7%; }

.eight-record-item {
  width: 300px;
  height: 520px;
  border-color: 1px solid #c4c3c3; }

.eight-record-explanation {
  margin: 1%; }

